.tech-container {
    display: flex;
    flex-direction: column
}

.tech-buttons-container {
    align-items: center;
    color: #fff;
    display: flex;
    font-size: 20px;
    gap: 2em;
    justify-content: space-evenly
}

.tech-button {
    flex-basis: 15%;
    padding: .5em;
    text-align: center;
    transition: .5s
}

.tech-button.active,
.tech-button:hover {
    border-bottom: 2px solid #00ffc6;
    color: #00ffc6;
    cursor: pointer;
    font-weight: 700;
    letter-spacing: .3em
}

.tech-content {
    justify-content: space-evenly;
    width: 100%
}

.tech-card-link,
.tech-content {
    align-items: center;
    display: flex;
    padding: 1rem
}

.tech-card-link {
    background-color: #121636;
    border-radius: 20px;
    color: #fff;
    flex-basis: 20%;
    flex-direction: column;
    height: 200px;
    justify-content: center;
    -webkit-transform-style: preserve-3d;
    transform-style: preserve-3d;
    transition: -webkit-transform 1s;
    transition: transform 1s;
    transition: transform 1s, -webkit-transform 1s;
    width: 200px
}

.tech-card-link:hover {
    -webkit-transform: rotateY(180deg);
    transform: rotateY(180deg)
}

.card-front {
    align-items: center;
    display: flex;
    flex-direction: column;
    gap: 1em
}

.card-back,
.card-front {
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden
}

.card-back {
    -webkit-transform: rotateY(180deg);
    transform: rotateY(180deg)
}

.card-back,
.tech-card-link:hover .card-front {
    display: none
}

.tech-card-link:hover .card-back {
    display: block
}

.goToTech {
    align-items: center;
    background: #000;
    border: 1px solid transparent;
    border-radius: 100px;
    color: #00ffc6;
    cursor: pointer;
    display: flex;
    font-family: Helvetica, "sans-serif";
    font-size: 15px;
    font-weight: 700;
    padding: 10px 20px;
    transition: all .2s
}

.goToTech:hover {
    background: #121636
}

.goToTech>svg {
    margin-left: 10px;
    transition: -webkit-transform .3s ease-in-out;
    transition: transform .3s ease-in-out;
    transition: transform .3s ease-in-out, -webkit-transform .3s ease-in-out;
    width: 34px
}

.goToTech:hover svg {
    -webkit-transform: translateX(5px);
    transform: translateX(5px)
}

.goToTech:active {
    -webkit-transform: scale(.95);
    transform: scale(.95)
}

