.team-wrapper {
    display: grid;
    -webkit-perspective: 40em;
    perspective: 40em;
    position: relative;
    -webkit-transform-style: preserve-3d;
    transform-style: preserve-3d
}

.team-card {
    align-items: center;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    background: linear-gradient(to left, var(--card-bg), #000);
    border-radius: 10px;
    box-shadow: 0 10px 30px -3px rgba(0, 0, 0, .1);
    color: #000;
    display: flex;
    grid-area: 1/1;
    height: 350px;
    justify-content: flex-start;
    padding: 30px;
    text-transform: uppercase;
    -webkit-transform: translateX(10px) rotateY(20deg) rotateX(10deg);
    transform: translateX(10px) rotateY(20deg) rotateX(10deg);
    width: 250px
}

h1 {
    font-size: 60px;
    font-weight: 900
}

.team-card .enclosed {
    background: #000;
    color: #f9c61a;
    display: inline-block;
    line-height: 1;
    padding: 0 5px;
    -webkit-transform: translate(-1px, 1px) scale(.75);
    transform: translate(-1px, 1px) scale(.75);
    -webkit-transform-origin: right center;
    transform-origin: right center
}

.team-img-container {
    height: 300px
}

.team-wrapper:before {
    --bw: 9px;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    background: transparent;
    border: var(--bw) solid hsla(0, 0%, 100%, .5);
    box-sizing: initial;
    content: "";
    grid-area: 1/1;
    height: 100%;
    margin-left: calc(var(--bw)*-1);
    margin-top: calc(var(--bw)*-1);
    pointer-events: none;
    -webkit-transform: translateX(-20px) rotateY(-20deg) rotateX(10deg) scale(1.02);
    transform: translateX(-20px) rotateY(-20deg) rotateX(10deg) scale(1.02);
    width: 100%
}

.team-wrapper:hover .team-card {
    background: linear-gradient(to bottom, var(--card-bg), #000)
}

.team-wrapper:hover .team-img-container {
    height: 200px
}

.team-wrapper:hover:before {
    border: var(--bw) solid #00ffc6;
    border-radius: 15px
}

.details-container {
    display: none;
    opacity: 0
}

.team-wrapper:hover .details-container {
    display: block;
    opacity: 1
}

.team-wrapper:hover:before,
.team-wrapper:hover>div {
    -webkit-transform: none;
    transform: none
}

.team-wrapper:before,
.team-wrapper>div {
    transition: transform .3s cubic-bezier(.25, .46, .45, 1);
    will-change: transform
}

body,
html {
    height: 100%
