}

button.visit {
    background: transparent;
    border: 0;
    cursor: pointer;
    display: inline-block;
    font-family: inherit;
    font-size: inherit;
    height: auto;
    outline: none;
    padding: 0;
    position: relative;
    text-decoration: none;
    vertical-align: middle;
    width: 12rem
}

button.visit .circle {
    background: #00ffc6;
    border-radius: 1.625rem;
    display: block;
    height: 3rem;
    margin: 0;
    position: relative;
    transition: all .45s cubic-bezier(.65, 0, .076, 1);
    width: 3rem
}

button.visit .circle .icon {
    background: #fff;
    bottom: 0;
    margin: auto;
    position: absolute;
    top: 0;
    transition: all .45s cubic-bezier(.65, 0, .076, 1)
}

button.visit .circle .icon.arrow {
    background: none;
    height: .125rem;
    left: .625rem;
    transition: all .45s cubic-bezier(.65, 0, .076, 1);
    width: 1.125rem
}

button.visit .circle .icon.arrow:before {
    border-right: .125rem solid #000;
    border-top: .125rem solid #000;
    content: "";
    height: .625rem;
    position: absolute;
    right: .0625rem;
    top: -.29rem;
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
    width: .625rem
}

button.visit .button-text {
    bottom: 0;
    color: #00ffc6;
    font-weight: 700;
    left: 0;
    letter-spacing: .1em;
    line-height: 1.6;
    margin: 0 0 0 1.85rem;
    padding: .75rem 0;
    position: absolute;
    right: 0;
    text-align: center;
    text-transform: uppercase;
    top: 0;
    transition: all .45s cubic-bezier(.65, 0, .076, 1)
}

button.visit:hover .circle {
    width: 100%
}

button.visit:hover .circle .icon.arrow {
    background: #000;
    -webkit-transform: translate(1rem);
    transform: translate(1rem)
}

button.visit:hover .button-text {
    color: #000
}

